// -----------------------------------------------------------------------------
// This file contains styles that are specific to the home page.
// -----------------------------------------------------------------------------
.home-slider {
    min-height: 659px;
    height: 659px;

    .slick-list,
    .slick-track {
        height: 100%;
    }

    .home-slider-slide-first {
        background: url("../img/home/main-banner-image.jpg") no-repeat center top;
        background-size: cover;
    }

    .home-slider-row {
        display: flex;
        justify-content: flex-end;
        padding-top: 192px;
    }
    
    .home-slider-column {
        // padding-right: 226px;
    }

    .home-slider-column-text {
        color: #fff;
        font-weight: 700;
        font-size: 45px;
        margin-bottom: 20px;
        // max-width: 757px;
        text-align: right;
        line-height: 75px;
    }

    .home-slider-button {
        margin-top: 35px;
        
        .col-12-m {
            text-align: right;
        }
    }
}

.about-us {
    position: relative;
    // height: 529px;

    .col-12-m {
        text-align: center;
        padding: 0 15px;

        h3 {
            text-transform: uppercase;
            color: #2B303A;
            font-size: 45px;
            font-weight: 700;
            padding-top: 67px;
            padding-bottom: 39px;
            margin-bottom: 0;
            line-height: 75px;
        }
    
        p {
            max-width: 991px;
            margin: 0 auto;
            padding-bottom: 17px;
            color: #2B303A;
            font-size: 20px;
            line-height: 24px;
            letter-spacing: 0;
        }
    }
}

.about-us::after {
    content: "";
    background: url("../img/home/about-image.jpg") no-repeat center center;
    background-size: cover;
    opacity: 0.2;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
    z-index: -1;   
}

.our-projects__wrapper {
    // position: relative;
    background: url("../img/home/our-projects-image.jpg") no-repeat center center;
    background-size: cover;
    z-index: -2;

    .our-projects-slider {
        padding-right: 52px;
        background-color: rgba(43, 48, 58, 0.8);            
    }

    h3 {
        color: #fff;
        text-transform: uppercase;
        font-size: 45px;
        // font-size: 3.363vw;
        font-weight: 700;
        line-height: 75px;
        padding: 70px 0 31px 0;
        margin-bottom: 0;
    }

    h4 {
        color: #fff;
        font-size: 24px;
        font-weight: 700;
        line-height: 115px;
        padding-bottom: 23px;
        margin-bottom: 0;
    }

    p {
        color: #fff;
        font-size: 20px;
        font-weight: 400;
        line-height: 24px;
        padding-bottom: 133px;
        margin-bottom: 0;
    }
}

