@media (max-width: 979px) {
    .our-projects__wrapper {
        
        .our-projects-slide {
            padding-left: 15px;
            padding-right: 15px;
        }
    }

    .our-projects__wrapper::after {
        width: 50%;
    }
}

@media (max-width: 767px) {
    .home-slider {
        min-height: auto;
        height: auto;

        .home-slider-row {
            display: block;
            padding-top: 110px;
            
            .home-slider-column {
                text-align: center;

                .home-slider-column-text {
                    text-align: center;
                    margin-bottom: 0;
                    line-height: 55px;
                }
            }
        }

        .home-slider-button {
            display: block;
            margin-bottom: 40px;
            
            .col-12-m {
                text-align: center;
            }
        }
    }

    .our-projects__wrapper {

        h3, h4, p {
            text-align: center;
        }

        .our-projects-slide {
            padding-left: 15px;
            padding-right: 15px;
        }
    }

    .our-projects__wrapper::after {
        width: 100%;
    }
}